import React from "react";

export const Card = ({details}) => {
    return(
        <div className="project-card">
            <h6>{details.title}</h6>
            <img className="project-duration" src={details.url} alt="" />
            <ul>
                {details.responsibilities.map((item) => (
                    <li key={item}>{item}</li>
                ))}
            </ul>
        </div>
    )
}